.app {
  min-height: 100%;
  height: 100%;
  position: relative;

  @media (max-width: 767px) {
    min-height: 100vh;;
  }

  &__row {
    display: flex;
   
    @media (max-width: 767px) {
      flex-direction: column;
    }
    
  }
}

.wallpaper {
  transition: 0.5s;
  position: absolute;
  top: 0;
  left: 0;
  min-height: 100%;
  width: 100%;
  z-index: -1;
}

.wallpaper::after {
  content: '';
  background: black;
  min-height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.4;
}

.wallpaper1 {
  background: url(./assets/images/wallpaper1.jpg) no-repeat center;
  background-attachment:fixed;
  background-size:cover;
}

