.menu {
    transition: width 0.3s;
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    width: 50px;
    background: white;
    border: 0 transparent;
    border-radius: 0 30px 30px 0 ;
    color: white;
    min-height: 100vh;
    overflow: hidden;

    @media (max-width: 767px) {
        width: 100%;
        flex-direction: row;
        gap: 20px;
        min-height: initial;
        border-radius: 0 0 30px 30px;
    }

    &__list {
        transition: width 0.3s;
        width: 100%;
        padding: 10px;

        @media(max-width: 767px) {
            display: flex;
            justify-content: center;
        }
    }

    &__avatar {
        img {
            max-width: 64px;
            max-height: 64px;
            width: 100%;
            margin: 0 0 20px 0;
            border: 0 transparent;
            border-radius: 100%;
        }
    }

    &__link{
        transition: 0.3s;
        
        display: block;
        color: black;
        font-size: 20px;
        margin: 0 0 20px 0;
        // border-bottom: 1px solid lightgrey;
        padding: 0 0 5px 0;
        // width: 100%;

        span {
            display: inline-flex;

            justify-content: flex-start;
            align-items: center;
            gap: 2px;
            width: 100%;
        }

        @media (max-width: 767px) {
            display: inline-flex;
            margin: 0 20px 0 0;
        }
    }

    &__link:hover {
        color: gold;
        // border-bottom: 1px solid gold;
    }

    &__link-active {
        color: gold;
        // border-bottom: 1px solid gold;
    }

    &__link-dark {
        color: white;
    }
}

.menu-dark {
    background: rgb(45, 45, 45);
}

