.settings {
    margin: 20px auto;
    padding: 0 10px;
    width: 100%;

    &__row {
        display: flex;
        flex-direction: column;
        margin: 0 auto;
        max-width: 800px;
    }
}

.settings-dark {
    div {
        background: rgb(45, 45, 45);
        color: white;
    }
}